.button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #cecece;
  padding: 10px 25px;
  border-radius: 8px;
  border: none;
  color: #fafafa;
  font-size: 16px;
  line-height: 22px;
  font-weight: 600;
  letter-spacing: .01rem;
  cursor: pointer;
}