@font-face {
  font-family: 'Nunito';
  src: url("fonts/nunito/Nunitoregular.woff2") format('woff2'),
  url("fonts/nunito/Nunitoregular.woff") format('woff'),
  url("fonts/nunito/Nunito-Regular.ttf") format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Nunito';
  src: url("fonts/nunito/Nunitosemibold.woff2") format('woff2'),
  url("fonts/nunito/Nunitosemibold.woff") format('woff'),
  url("fonts/nunito/Nunito-SemiBold.ttf") format('truetype');
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: url("fonts/montserrat/Montserrat-SemiBold.ttf") format('truetype');
  font-weight: 600;
  font-style: normal;
}